body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg{
  width: 500px;
  height: 200px;
  border-radius: 100px;
  transition: all 1s;
}

.bgOff{
  background-color: rgb(56, 56, 56);
}

.bgOn{
  background-color: rgb(79, 100, 76);
}

.slider{
  width: 200px;
  height: 200px;
  border-radius: 100px;
  transition: all 0.5s;
}
.sliderOff{
  background-color: rgb(117, 117, 117);
}

.sliderOn{
  margin-left: 300px;
  background-color: rgb(114, 173, 103);
}
.container{
  display: flex;
  width: 100%;
  height: 90%;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  padding-bottom: 2%;
}

.tile{
  width: 50px;
  height: 50px;
  transition: all 0.4s;
}

.on{
  background-color: rgb(182, 182, 182);
}
.off{
  background-color: black;
}

.boardRow{
  display: flex;
  flex-direction: row;
}

.message{
  width: 100%;
  padding-top: 4px;
  padding-bottom: 4px;
}

.message:nth-child(odd){
  background-color: rgb(231, 231, 231);
}
.messagesContainer{
  height: 700px;
  width: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

.ChatContainer{
  
  display: flex;
  width: 50%;
  height: 90%;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  padding-bottom: 2%;
}

.input{
  width: 100%;
  display: flex;
  flex-direction: row;
  
}

.inputBox{
  width: 90%;
  padding: 2px;
}
